<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right" style="height: 4%;">
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<div class="PersonnalDetails">
			<el-divider content-position="left"></el-divider>
			<div class="panel-body" style="height: 90%; overflow-x:hidden">
			<!--  -->
			<el-card v-if="modal==='1'">
				<div style="margin-left: 10px;">
					<h4>id{{userID}}</h4>
					<h4>{{companyID}}</h4>
				</div>
				<el-button style="margin-left: 10px; margin-bottom: 10px; float:left; margin-right: 55px;" @click="dialogVisible = true">
					
				</el-button>
				<el-dialog title="" :visible.sync="dialogVisible" width="3" append-to-body>
					<!--  -->
					<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
						<el-form-item label="id" prop="factory_id">
							<el-input v-model="addForm.factory_id"></el-input>
						</el-form-item>
					</el-form>
					
					<span slot="footer" class="dialog-footer">
						<el-button @click="dialogVisible = false"> </el-button>
						<el-button type="primary" @click="dialogVisible=false;Applying()"> </el-button>
					</span>
				</el-dialog>
			</el-card>
			
			<!--  -->
			<el-card v-if="modal==='2'">
				<div style="margin-left: 10px;">
					<h4>id{{userID}}</h4>
					<h4>{{companyID}}</h4>
					<h4></h4>
				</div>
				<el-button stripe style="width: 200px" border @click="fetchData1" title="">
					
				</el-button>
				<el-button stripe style="width: 200px" border @click="fetchData2" title="">
					
				</el-button>
				<el-table v-if="requestList.length > 0" :data="requestList" stripe style="width: 981px" border>
					<el-table-column type="index" label="#" width="80px"></el-table-column>
					<el-table-column prop="company_id" label="id" width="225px"></el-table-column>
					<el-table-column prop="user_id" label="id" width="225px"></el-table-column>
					<el-table-column prop="dismiss" label="" width="225px"></el-table-column>
					<el-table-column prop="" label="" width="225px">
						<template slot-scope = "scope">
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="success" icon="el-icon-check" size="small" @click="Register1(scope.row)" v-if="scope.row.dismiss === ''">
								</el-button>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="danger" icon="el-icon-delete" size="small" @click="Register2(scope.row)" v-if="scope.row.dismiss === ''">
								</el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
		
				<el-table v-if="personList.length > 0" :data="personList" stripe style="width: 981px" border>
					<el-table-column type="index" label="#" width="50px"></el-table-column>
					<el-table-column prop="company" label="id" width="75px"></el-table-column>
					<el-table-column prop="user_id" label="id" width="100px"></el-table-column>
					<el-table-column prop="username" label="" width="100px"></el-table-column>
					<el-table-column prop="modal" label="" width="100px"></el-table-column>
					<el-table-column prop="email" label="" width="100px"></el-table-column>
					<el-table-column prop="phoneNumber" label="" width="150px"></el-table-column>
					<el-table-column prop="otherplat" label="" width="100px"></el-table-column>
					<el-table-column prop="dismiss" label="" width="75px"></el-table-column>
					<el-table-column prop="" label="" width="130px">
						<template slot-scope = "scope">
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="danger" icon="el-icon-delete" size="small" @click="Register4(scope.row)"></el-button>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="success" icon="el-icon-check" size="small" @click="Register3(scope.row)" v-if="scope.row.modal !== ''">
								</el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</el-card>
			
			<!--  -->
			<el-card v-if="modal==='3'">
				<div style="margin-left: 10px;">
					<h4></h4>
				</div>
				<el-button stripe style="width: 200px" border @click="fetchData3" title="">
					
				</el-button>
				
				<el-table v-if="allperList.length > 0" :data="allperList" stripe style="width: 981px" border>
					<el-table-column type="index" label="#" width="50px"></el-table-column>
					<el-table-column prop="company" label="id" width="70px"></el-table-column>
					<el-table-column prop="user_id" label="id" width="100px"></el-table-column>
					<el-table-column prop="username" label="" width="100px"></el-table-column>
					<el-table-column prop="modal" label="" width="75px"></el-table-column>
					<el-table-column prop="email" label="" width="100px"></el-table-column>
					<el-table-column prop="phoneNumber" label="" width="150px"></el-table-column>
					<el-table-column prop="otherplat" label="" width="80px"></el-table-column>
					<el-table-column prop="dismiss" label="" width="75px"></el-table-column>
					<el-table-column prop="" label="" width="180px">
						<template slot-scope = "scope">
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="success" icon="el-icon-check" size="small" @click="Register5(scope.row)" v-if="scope.row.modal !== ''">
								</el-button>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="warning" icon="el-icon-check" size="small" @click="Register3(scope.row)" 
								v-if="scope.row.company !== '0' && scope.row.modal === ''">
								</el-button>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="danger" icon="el-icon-delete" size="small" @click="Register4(scope.row)" 
								v-if="scope.row.company !== '0'"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</el-card>
