</template>

<script>
	import axios from 'axios';
	import Cookies from 'js-cookie' ;
	
export default{
	name: 'PersonnalDetails',
	data() {
		return {
			token: '',
			userID: '',
			modal: '',
			companyID: '',
			requestList: [],
			personList: [],
			allreqList: [],
			allperList: [],
			requestID: '',
			companyID1: '',
			tuserID: '',
			dialogVisible: false,
			//
			addForm: {
				factory_id: ''
			},
			//
			addFormRules: {
				factory_id: [{
					required: true,
					message: 'id',
					trigger: 'blur'}, { 
					min: 1, 
					max: 11, 
					message: 'id1~11', 
					trigger: 'blur'}]
			}
		}
	},
	created(){
		this.token = Cookies.get("token");
		this.userID = Cookies.get("user_id");
		this.modal = Cookies.get("modal");
		this.companyID = Cookies.get("company_id");
	},
	methods:{
		fetchData1(){
			var that = this
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '014',
						user_id: that.userID,
						token: that.token,
						company_id: that.companyID
					}
				})
				.then(function(response){
					console.log(response.data);
					that.requestList = response.data.data;
					that.ChangeDismiss1();
				})
				.catch(function(error){
					console.log(error);
				})
		},
		ChangeDismiss1(){
			var that = this;
			for(var i = 0; i < that.requestList.length; i++)
			{
				if(that.requestList[i].dismiss === '0'){
					that.requestList[i].dismiss = '';
				}
				else if(that.requestList[i].dismiss === '1'){
					that.requestList[i].dismiss = '';
				}
				else{
					that.requestList[i].dismiss = '';
				}
			}
		},
		Applying(){
			var that = this
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '013',
						user_id: that.userID,
						token: that.token,
						company_id: that.addForm.factory_id
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
		},
		Register1(event){
			var that = this;
			console.log(event);
			that.requestID = event.request_id;
			that.companyID1 = event.company_id;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '015',
						user_id: that.userID,
						company_id: that.companyID1,
						token: that.token,
						type: '1',
						request_id: that.requestID
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
			alert("");
		},
		Register2(event){
			var that = this;
			console.log(event);
			that.requestID = event.request_id;
			that.companyID1 = event.company_id;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '015',
						user_id: that.userID,
						company_id: that.companyID1,
						token: that.token,
						type: '0',
						request_id: that.requestID
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
			alert("");
		},
		fetchData2(){
			var that = this
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '016',
						user_id: that.userID,
						token: that.token,
						company_id: that.companyID
					}
				})
				.then(function(response){
					console.log(response.data);
					that.personList = response.data.data;
					that.ChangeDismiss2();
				})
				.catch(function(error){
					console.log(error);
				})
		},
		ChangeDismiss2(){
			var that = this;
			for(var i = 0; i < that.personList.length; i++)
			{
				that.personList[i].dismiss = '';
				if(that.personList[i].modal === '1'){
					that.personList[i].modal = '';
				}
				else if(that.personList[i].modal === '2'){
					that.personList[i].modal = '';
				}
			}
		},
		Register3(event){
			var that = this;
			console.log(event);
			that.tuserID = event.user_id;
			that.companyID1 = event.company;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '017',
						user_id: that.userID,
						company_id: that.companyID1,
						token: that.token,
						type: '1',
						target_user_id: that.tuserID
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
			alert("");
		},
		Register4(event){
			var that = this;
			console.log(event);
			that.tuserID = event.user_id;
			
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '018',
						user_id: that.userID,
						company_id: that.companyID,
						token: that.token,
						target_user_id: that.tuserID
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
			alert("");
		},
		fetchData3(){
			var that = this
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '022',
						user_id: that.userID,
						token: that.token
					}
				})
				.then(function(response){
					console.log(response.data);
					that.allperList = response.data.data;
					that.ChangeDismiss3();
				})
				.catch(function(error){
					console.log(error);
				})
		},
		ChangeDismiss3(){
			var that = this;
			for(var i = 0; i < that.allperList.length; i++)
			{
				that.allperList[i].dismiss = '';
				if(that.allperList[i].modal === '1'){
					that.allperList[i].modal = '';
				}
				else if(that.allperList[i].modal === '2'){
					that.allperList[i].modal = '';
				}else{
					that.allperList[i].modal = '';
				}
			}
		},
		Register5(event){
			var that = this;
			console.log(event);
			that.tuserID = event.user_id;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '017',
						user_id: that.userID,
						company_id: that.companyID,
						token: that.token,
						type: '2',
						target_user_id: that.tuserID
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
			alert("");
		}
	}
}	
</script>

<style scoped>
.PersonnalDetails{/* workspack */
	width: 100%;
	height: 96%;
	background-color: #FFFFFF;
/* 	border: black;
	border-width: 1px;
	border-style: solid; */
}
.el-card{
	margin-top: 20px;
	margin-left: 20px;
