<template>
	<div class="loginBox">
		<div class="loginContainer">
			<div style="text-align: left; width: 400px">
				<h2></h2>
			</div>
			<div class="login">
				<el-form ref="form" :model="form">
					<el-form-item>
						<el-input v-model="form.name" placeholder="/"></el-input>
					</el-form-item>
					<el-form-item>
						<el-input v-model="form.password" placeholder="" show-password></el-input>
					</el-form-item>
					<el-form-item>
						<el-checkbox style="float: left" label="" v-model="checked"></el-checkbox>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" style="width: 400px" @click="loading()">  </el-button>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" style="width: 400px" @click="register()">  </el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	import sha256 from 'js-sha256'
	import axios from 'axios'
	import Qs from 'qs'
	export default {
		name: 'Loading',
		data() {
			return {
				token: '',
				user_id: '',
				checked: false,
				form: {
					name: '',
					password: '',
				},
			}
		},
		mounted() {
			this.getNameCookie();
			this.getIdCookier();
			if(this.user_id!==''&&this.token!==''&&this.company_id!==''&&this.modal!==''){
				this.$router.push('/mainPage/dataInformation')
			}
		},
		methods: {
			//
			loading() {
				let _this = this

				let sha256 = require("js-sha256").sha256 //requireimport
				var sha256password = sha256(this.form.password) //
				// console.log(sha256password)//

				var way;
				if (/^(0|86|17951)?(13[0-9]|15[012356789]|17[0-8]|18[0-9]|14[5679]|19[189]|16[56])[0-9]{8}$/.test(this.form.name)) {
					way = 1;
				} else if (/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+$/.test(this.form.name)) {
					way = 2;
				} else {
					way = 3;
					console.log('');
				}
				// console.log(way);

				if (this.form.name === '' || this.form.password === '') {
					alert('')
				} else {
					if (_this.checked) {
						_this.setNameCookie(_this.form.name, _this.form.password, 7)
					} else {
						_this.clearNameCookie()
					}
					
					var url = 'https://site.maple.today/IOTplatform/ServerMain';
					let data = {
						"requestCode": "002",
						"account": this.form.name,
						"loginway": way,
						"password": sha256password,
					};
					console.log(Qs.stringify(data));
					var AXIOS = axios.create({
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded'
						}
					});
					AXIOS.post(url, Qs.stringify(data))
						.then(res => {
							if (res.data.result === "1") {
								console.log('');
								console.log(res);
								_this.setIdCookie(res.data.user_id,res.data.token,res.data.company,res.data.modal,this.form.password,1);
								// _this.$router.push({
								// 	name:'MainPage',
								// })
								_this.$router.push('/mainPage/dataInformation')
							} else if (res.data.result === "0") {
								alert('')
							} else if (res.data.result === "2") {
								alert('')
							}
							else{
								console.log(res)
							}
						})
						.catch(error => console.log(error));
				}
			},
			
			//
			register() {
				this.$router.push({
					name: 'Register',
				})
			},
			
			//setNameCookie
			setNameCookie(c_name, c_pwd, exdays) {
				var exdate = new Date() //
				exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) //
				//cookie
				window.document.cookie = 'userName' + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'userPwd' + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
			},
			setIdCookie(c_id,c_token,c_companyid,c_modal,c_password,exdays){
				//c_idid
				//c_tokentoken
				//c_companyidid
				//c_modal
				var exdate = new Date() //
				exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) //
				//cookie
				window.document.cookie = 'user_id' + '=' + c_id + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'token' + '=' + c_token + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'company_id' + '=' + c_companyid + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'modal' + '=' + c_modal + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'password' + '=' + c_password + ';path=/;expires=' + exdate.toGMTString()
			},
			//cookie
			getNameCookie: function() {
				if (document.cookie.length > 0) {
					this.checked = true
					// console.log(document.cookie)
					var arr = document.cookie.split('; ') //
					for (var i = 0; i < arr.length; i++) {
						var arr2 = arr[i].split('=') //
						//
						if (arr2[0] === 'userName') {
							this.form.name = arr2[1] //
						}
						if (arr2[0] === 'userPwd') {
							this.form.password = arr2[1]
						}
					}
				}
			},
			getIdCookier: function(){
				if (document.cookie.length > 0) {
					// console.log(document.cookie)
					var arr = document.cookie.split('; ') //
					for (var i = 0; i < arr.length; i++) {
						var arr2 = arr[i].split('=') //
						//
						if (arr2[0] === 'user_id') {
							this.user_id = arr2[1] //
						}
						if (arr2[0] === 'token') {
							this.token = arr2[1]
						}
						if (arr2[0] === 'company_id') {
							this.company_id = arr2[1]
						}
						if (arr2[0] === 'modal') {
							this.modal = arr2[1]
						}
					}
				}
			},
			//cookie
			clearNameCookie: function() {
				this.setNameCookie('', '', -1) //21
			},
			clearIdCookie: function() {
				this.setIdCookie('', '','','','', -1) //21
			},
		},
	}
</script>

<style>
	.loginBox {
		position: absolute;
		height: 100%;
		width: 100%;
		/* background: pink; */
		margin: 0;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.loginContainer {
		width: 480px;
		background: white;
		display: flex;
		flex-direction: column;
		align-items: center;
		border-radius: 6px;
	}

	h2 {
		margin-bottom: 50px;
		margin-top: 50px;
	}

	.login {
		width: 400px;
		margin-bottom: 50px;
	}
</style>
