<template>
	<div>
		<el-breadcrumb separator-class="el-icon-arrow-right" style="height: 4%;">
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item :to="{name:'DeviceManage'}" v-if="this.whichFrom===2"></el-breadcrumb-item>
			<el-breadcrumb-item :to="{name:'ProductionManage'}" v-if="this.whichFrom===1"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<div class="addCompony">
			<el-divider content-position="right"></el-divider>
			<div style="width: 100%;height: 80%;" ref="scroll">
				<el-scrollbar style="height: 100%; width: 100%;">
					<div class="form">
						<el-form ref="form" :model="componyForm" label-width="80px">
							<el-form-item label="">
								<el-input v-model="componyForm.name"></el-input>
							</el-form-item>
							<el-form-item label="()">
								<div style="text-align: right;">
									<select id="province" style="width: 130px; height: 30px; border: 1px solid #ccc"
										@change="search"></select>
									<select id="city" style="width: 130px; height: 30px; border: 1px solid #ccc"
										@change="search"></select>
									<select id="district" style="width: 130px; height: 30px; border: 1px solid #ccc"
										@change="search"></select>
								</div>
								<div id="container">
								</div>
							</el-form-item>
							<el-form-item label="" v-if="componyForm.position.lat!==null">
								<el-tag style="margin-right: 20px;">{{componyForm.position.lng}}</el-tag>
								<el-tag>{{componyForm.position.lat}}</el-tag>
							</el-form-item>
						</el-form>
					</div>
				</el-scrollbar>
			</div>
			<div
				style="display: flex;flex-direction: row-reverse;padding-right: 10px;height: 10%;padding-bottom: 10px;">
				<el-button type="success" v-on:click="creat()"></el-button>
			</div>

		</div>
	</div>
</template>

<script>
	import Cookies from 'js-cookie'
	import {
		AMapManager
	} from 'vue-amap';
	// import AMap from 'Amap'
	export default {
		data() {
			let self = this;

			return {
				marker: null,
				position: {
					lat: null,
					lng: null
				},
				map: null,
				polygons: [],
				district: null,
				componyForm: {
					name: "",
					position: {
						lat: null,
						lng: null
					},
				},
				events: {
					click(e) {
						let {
							lng,
							lat
						} = e.lnglat;
						self.lng = lng;
						self.lat = lat;
						alert(self.lng + "/" + self.lat)
					}
				}
			}
		},
		methods: {

			province() {
				// 
				let opts = {
					subdistrict: 1, // 
					showbiz: false // 
				}
				this.district = new AMap.DistrictSearch(opts) // 
				this.district.search('', (status, result) => {
					if (status === 'complete') {
						this.getData(result.districtList[0])
					}
				})
			},
			getData(data, level) {
				let citySelect = document.getElementById('city')
				let districtSelect = document.getElementById('district')
				let bounds = data.boundaries
				var that = this
				if (bounds) {

					for (let i = 0, l = bounds.length; i < l; i++) {
						let polygon = new AMap.Polygon({
							map: this.map,

							strokeWeight: 1,
							strokeColor: '#ff52a1',
							fillColor: '#ffeef6',
							fillOpacity: 0.2,
							path: bounds[i],

						})
						AMap.event.addListener(polygon, 'click', function(e) { //,
							that.position = e.lnglat
							// alert(this.position)
							if(that.marker !== null){
								that.map.remove(that.marker)	
							}
							that.marker =  new AMap.Marker({
							position: [e.lnglat.lng, e.lnglat.lat],
							})
							that.marker.setMap(that.map)
							that.componyForm.position = e.lnglat
						})
						this.polygons.push(polygon)
					}
					if(this.marker !== null){
						this.map.remove(that.marker)	
					}
					this.componyForm.position.lat = null
					this.componyForm.position.lng =null
					this.map.setFitView() // 
				}
				// 
				if (level === 'province') {
					citySelect.innerHTML = ''
					districtSelect.innerHTML = ''
				} else if (level === 'city') {
					districtSelect.innerHTML = ''
				}
				let subList = data.districtList
				if (subList) {
					let contentSub = "";
					let curlevel = subList[0].level;
					let curList = document.querySelector("#" + curlevel);
					if (!curList) return false;
					if (curlevel === "province") {
						curList.add(new Option(""));
					} else if (curlevel === "city") {
						curList.add(new Option(""));
					} else {
						curList.add(new Option(""));
					}
					for (let i = 0, l = subList.length; i < l; i++) {
						let name = subList[i].name
						let levelSub = subList[i].level
						contentSub = new Option(name)
						contentSub.setAttribute('value', levelSub)
						contentSub.center = subList[i].center
						contentSub.adcode = subList[i].adcode
						curList.add(contentSub)
					}
				}
			},
			search(obj) {
				// 
				let that = this
				for (let i = 0, l = this.polygons.length; i < l; i++) {
					this.polygons[i].setMap(null)
				}
				let option = obj.srcElement[obj.srcElement.options.selectedIndex]
				let adcode = option.adcode
				this.district.setLevel(option.value) // 
				this.district.setExtensions('all')
				// 
				// adcode
				this.district.search(adcode, function(status, result) {
					if (status === 'complete') {
						that.getData(result.districtList[0], obj.srcElement.id)
					}
				})
			},
			loadmap() {
				var that = this
				this.map = new AMap.Map('container', {
					center: [118.792221, 31.938254],
					zoom: 15,
				});
				// this.marker = new AMap.Marker({
				// 	position: [118.792221, 31.938254],
				// 	// icon: alarmIcon, // Iconconst
				// 	// offset: new AMap.Pixel(-25, -35)
				// })
				// this.marker.setMap(this.map)
						// 
				// this.map.setFitView([this.maker])
				AMap.event.addListener(this.map, 'click', function(e) {
					//,
					if(that.marker !== null)
					that.map.remove(that.marker)		
					that.marker =  new AMap.Marker({
					position: [e.lnglat.lng, e.lnglat.lat],
				    })
					that.marker.setMap(that.map)
					that.componyForm.position = e.lnglat
				})
			},
			creat() {
				var that = this
				if (this.componyForm.name === "" || this.componyForm.position.lat === null) {
					this.$message({
						message: '',
						type: 'warning'
					});
				} else {
					// this.$message({
					// 	message: '',
					// 	type: 'success'
					// });
					this.$axios.get('https://site.maple.today/IOTplatform/ServerMain', {
						params: {
							requestCode : '024',
							user_id : Cookies.get("user_id"),
							token : Cookies.get("token"),
							name : this.componyForm.name,
							latitude : this.componyForm.position.lat,
							longitude : this.componyForm.position.lng,
						},
					})
					.then(function(response){
						console.log(response)
						if(response.data !== undefined){
							that.$message({
								message: '',
								type: 'success'
							});
							if(that.whichFrom === 1)
							that.$router.push({name :"ProductionManage"})
							else
							that.$router.push({name :"DeviceManage"})
						}else{
							that.$message({
								message: '',
								type: 'warning'
							});
						}
					})
					.catch(function(error){
						console.log(error)
					})
				}
			}
		},
		mounted() {
			this.loadmap()
			this.province()
			console.log("from:" + this.whichFrom)
		},
		watch: {
		    componyForm : function(){
				console.log("change")
			}
		},
		props: ['whichFrom']
	}
</script>
<style>
	#container {
		width: 100%;
		height: 300px;
	}

	.addCompony {
		width: 100%;
		height: 96%;
	}

	.amap-page-container {
		width: 500px;
		height: 300px;
	}
</style>
