<template>
	<div>
		<!--  --><!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right" style="height: 4%;">
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item :to="{name:'DataInformation'}"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<div class="ViewDetails">
			<el-divider content-position="left"></el-divider>
			<div class="panel-body" style="height: 90%; overflow-x:hidden">
			<!--  -->
			<el-card v-if="modal==='3'">
				<!--  -->
				<el-table  @row-click="GetCompany" :data="companyList" stripe style="width: 981px" border title="">
					<el-table-column type="index" label="#" width="280px"></el-table-column>
					<el-table-column prop="company_name" label="" width="700px" >
					</el-table-column>
				</el-table>
				<!--  -->
				<el-table v-if="gatewayList.length > 0" @row-click="GetChart" :data="gatewayList" stripe style="width: 981px" border title="">  
					<el-table-column type="index" label="#" width="60px"></el-table-column>
				    <el-table-column prop="device_name" label="" width="200px"></el-table-column>
				    <el-table-column prop="device_id" label="id" width="200px"></el-table-column>
					<el-table-column prop="dismiss" label="" width="200px"></el-table-column>
					<el-table-column prop="company" label="" width="200px"></el-table-column>
					<el-table-column label="" width="120px">
						<template slot-scope = "scope">
							<el-tooltip class="item" effect="dark" content="" placement="top-start" :enterable="false">
								<el-button type="primary" icon="el-icon-edit" size="small" @click="Register(scope.row)"
								v-if="scope.row.dismiss===''">
								</el-button>								
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
				<div id="EchartsView" style="width: 800px; height:300px; padding-left: 80px; margin-top: 50px;"></div>
			</el-card>
			<!--  -->
			<el-card v-if="modal==='2'">
				<el-button style="margin-left: 10px; margin-bottom: 10px; float:right; margin-right: 55px;" @click="dialogVisible = true">
					
				</el-button>
				<el-dialog title="" :visible.sync="dialogVisible" width="3" append-to-body>
					<!--  -->
					<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
						<el-form-item label="" prop="device_name">
							<el-input v-model="addForm.device_name"></el-input>
						</el-form-item>
					</el-form>
					<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
						<el-form-item label="" prop="password">
							<el-input v-model="addForm.password"></el-input>
						</el-form-item>
					</el-form>
					
					<span slot="footer" class="dialog-footer">
						<el-button @click="dialogVisible = false"> </el-button>
						<el-button type="primary" @click="dialogVisible=false;Adding()"> </el-button>
					</span>
				</el-dialog>
				<!--  -->
				<el-table @row-click="GetChart" :data="gatewayList" stripe style="width: 981px" border title="">  
					<el-table-column type="index" label="#" width="80px"></el-table-column>
				    <el-table-column prop="device_name" label="" width="225px"></el-table-column>
				    <el-table-column prop="device_id" label="id" width="225px"></el-table-column>
					<el-table-column prop="dismiss" label="" width="225px"></el-table-column>
					<el-table-column prop="company" label="" width="225px"></el-table-column>
				</el-table>
				<div id="EchartsView" style="width: 800px; height:300px; padding-left: 80px; margin-top: 50px;"></div>
			</el-card>
			<!--  -->
			<el-card v-if="modal==='1'">
				<!--  -->
				<el-table @row-click="GetChart" :data="gatewayList" stripe style="width: 981px" border title="">  
					<el-table-column type="index" label="#" width="80px"></el-table-column>
				    <el-table-column prop="device_name" label="" width="225px"></el-table-column>
				    <el-table-column prop="device_id" label="id" width="225px"></el-table-column>
					<el-table-column prop="dismiss" label="" width="225px"></el-table-column>
					<el-table-column prop="company" label="" width="225px"></el-table-column>
				</el-table>
				<div id="EchartsView" style="width: 800px; height:300px; padding-left: 80px; margin-top: 50px;"></div>
			</el-card>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie' ;
import sha256 from 'js-sha256';
import * as echarts from 'echarts';
export default{
	name: 'ViewDetails',
	data() {
		return {
			//
			gatewayList: [],
			companyList: [],
			dataList: [],
			deviceidList: [],
			datanumList: [],
			deviceID: '',
			userID: '',
			token: '',
			companyID: '',
			modal: '',
			deviceid: '',
			tag: '0',
			companyID1: '',	//
			dialogVisible: false,
			//
			addForm: {
				device_name: '',
				password: ''
			},
			//
			addFormRules: {
				device_name: [{
					required: true,
					message: '',
					trigger: 'blur'}, { 
					min: 1, 
					max: 40, 
					message: '1~40', 
					trigger: 'blur'}],
				password: [{
					required: true,
					message: '',
					trigger: 'blur'}, { 
					min: 1, 
					max: 10, 
					message: '1~10', 
					trigger: 'blur'
				}]
			}
			// addFormRef: {}
		}
	},
	created(){
		this.token = Cookies.get("token");
		this.userID = Cookies.get("user_id");
		this.modal = Cookies.get("modal");
		this.companyID = Cookies.get("company_id");
		if(this.modal === '3'){
			this.fetchCData();//
		}else{
			this.fetchGData();//
		}
		// this.fetchGData();
	},
	methods:{
		GetChart(e){
			var that = this;
			that.deviceID = e.device_id;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '028',
						user_id: that.userID,
						company_id: that.companyID,
						token: that.token
					}
				})
				.then(function(response){
					console.log(response.data);
					that.dataList = response.data.data;
					for(var i = 0 ; i < that.dataList.length; i++){
						if(that.dataList[i].net_id === that.deviceID){
							that.deviceidList.push(that.dataList[i].device_id);
							that.datanumList.push(that.dataList[i].dataNumber);
							that.tag = '1';
						}
					}
					if(that.tag === '0'){
						// that.deviceidList.splice(0,that.deviceidList.length);
						// that.datanumList.splice(0,that.datanumList.length);
						that.deviceidList = [];
						that.datanumList = [];
						alert("");
					}else{
						let myChart = that.$echarts.init(document.getElementById("EchartsView"));
						// 
						let option = {
							title: {
								text: "24"
							},
							tooltip: {},
							legend: {
									data: [""]
								},
							xAxis: {
								name: 'id',
								data: that.deviceidList
							},
							yAxis: {
								
							},
							series: [{
								name: "",
								type: "bar",
								data: that.datanumList
							}]
						};
						// 
						myChart.setOption(option);
						that.tag = '0';
						that.Gotohere();
					}
				})
				.catch(function(error){
					console.log(error);
				})
		},
		Gotohere(){
			this.$el
			    .querySelector("#EchartsView")
			    .scrollIntoView({ block: "start", behavior: "smooth" });
		},
		fetchCData(){
			var that = this;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '011',
						user_id: that.userID,
						token: that.token
					}
				})
				.then(function(response){
					console.log(response.data);
					that.companyList = response.data.data;
				})
				.catch(function(error){
					console.log(error);
				})
		},
		fetchGData(){
			//post
			// 
			var that = this;
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '003',
						user_id: that.userID,
						company_id: that.companyID,
						token: that.token
					}
				})
				.then(function(response){
					console.log(response.data);
					that.gatewayList = response.data.data;
					that.ChangeDismiss();
				})
				.catch(function(error){
					console.log(error);
				})
		},
		ChangeDismiss(){
			var that = this;
			for(var i = 0; i < that.gatewayList.length; i++)
			{
				if(that.gatewayList[i].dismiss === '1'){
					that.gatewayList[i].dismiss = '';
				}
				else if(that.gatewayList[i].dismiss === '2'){
					that.gatewayList[i].dismiss = '';
				}
				else{
					that.gatewayList[i].dismiss = '';
				}
			}
		},
		GetCompany(event){
			// console.log(event);
			var that = this;
			that.companyID = event.company_id;
			// console.log(that.companyID);
			that.fetchGData();
		},
		Register(event)
		{
			var that = this;
			that.deviceid = event.device_id;
			that.companyID1 = event.company;

			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '009',
						user_id: that.userID,
						company_id: that.companyID1,
						token: that.token,
						device_id: that.deviceid
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
			alert("");

		},
		handleClose(done) {
		        this.$confirm('')
		          .then(_ => {
		            done();
		          })
		          .catch(_ => {});
		},
		Adding(){
			let that = this
			//console.log(that.addForm.password)
			let sha256 = require("js-sha256").sha256 //requireimport
			var sha256password = sha256(that.addForm.password) //
			//console.log(sha256password)//
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '008',
						user_id: that.userID,
						company_id: that.companyID,
						token: that.token,
						device_name: that.addForm.device_name,
						password: sha256password
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
		}
	}
}	
</script>

<style scoped>
.ViewDetails{/* workspack */
	width: 100%;
	height: 96%;
/* 	background-color: antiquewhite;
	border: black;
	border-width: 1px;
	border-style: solid; */
}
.el-card{
	width: 96%;
	margin-top: 20px;
	margin-left: 20px;
}
.el-table{
	margin-top: 15px;
}
</style>