<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right" style="height: 4%;">
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<div class="Settings">
			<!--  -->
				<el-divider content-position="left"></el-divider>
			<el-card>
				<el-button style="margin-left: 10px; margin-bottom: 10px;" @click="dialogVisible = true">
					StreamSets
				</el-button>
				<el-dialog title="StreamSets" :visible.sync="dialogVisible" width="3" append-to-body>
					<!--  -->
					<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
						<el-form-item label="" prop="password">
							<el-input v-model="addForm.password"></el-input>
						</el-form-item>
					</el-form>
					
					<span slot="footer" class="dialog-footer">
						<el-button @click="dialogVisible = false"> </el-button>
						<el-button type="primary" @click="dialogVisible=false;Changepassword()"> </el-button>
					</span>
				</el-dialog>				
				<br>
				<el-button style="margin-left: 10px; margin-bottom: 10px; margin-top: 10px;" @click="gotolink8">
					
				</el-button>								
				<br>
				<el-button style="margin-left: 10px; margin-bottom: 10px; margin-top: 10px;" @click="dialogVisible1 = true">
					
				</el-button>
				<el-dialog title="" :visible.sync="dialogVisible1" width="3" append-to-body>
					<!--  -->
					<el-form :model="addForm1" :rules="addFormRules1" ref="addFormRef1" label-width="100px">
						<el-form-item label="" prop="username">
							<el-input v-model="addForm1.username"></el-input>
						</el-form-item>
					</el-form>
					
					<span slot="footer" class="dialog-footer">
						<el-button @click="dialogVisible1 = false"> </el-button>
						<el-button type="primary" @click="dialogVisible1=false;Changeusername()"> </el-button>
					</span>
				</el-dialog>		
				
				<br>
				<el-button style="margin-top: 10px; margin-bottom: 10px; margin-left: 10px;" @click="gotolink6">
					
				</el-button>
			</el-card>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import Cookies from 'js-cookie' ;
	
export default{
	name: 'Settings',
	data() {
		return {
			token: '',
			userID: '',
			modal: '',
			companyID: '',
			dialogVisible: false,
			dialogVisible1: false,
			//
			addForm: {
				password: ''
			},
			//
			addFormRules: {
				password: [{
					required: true,
					message: 'StreamSets',
					trigger: 'blur'}, { 
					min: 6, 
					max: 18, 
					message: 'StreamSets6~18', 
					trigger: 'blur'
				}]
			},
			addForm1: {
				username: ''
			},
			addFormRules1: {
				username: [{
					required: true,
					message: '',
					trigger: 'blur'}, { 
					min: 1, 
					max: 40, 
					message: '1~40', 
					trigger: 'blur'
				}]
			}
		}
	},
	created(){
		this.token = Cookies.get("token");
		this.userID = Cookies.get("user_id");
		this.modal = Cookies.get("modal");
		this.companyID = Cookies.get("company_id");
	},
	methods:{
		Changeusername(){
			var that = this
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '033',
						user_id: that.userID,
						token: that.token,
						username: that.addForm1.username
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
		},
		Changepassword(){
			var that = this
			axios.get(
				"https://site.maple.today/IOTplatform/ServerMain",
				{
					params:{
						requestCode: '023',
						user_id: that.userID,
						token: that.token,
						password: that.addForm.password
					}
				})
				.then(function(response){
					console.log(response.data);
				})
				.catch(function(error){
					console.log(error);
				})
		},
		gotolink6(){
			Cookies.remove('token');
			Cookies.remove('user_id');
			Cookies.remove('modal');
			Cookies.remove('company_id');
			this.$router.push({name: "Loading"})
		},
		gotolink8(){
			alert("\n")
			Cookies.remove('token');
			Cookies.remove('user_id');
			Cookies.remove('modal');
			Cookies.remove('company_id');
			this.$router.push({name: "Loading"})
		}
	}
}	
</script>

<style scoped>
.Settings{/* workspack */
	width: 100%;
	height: 96%;
	/* background-color: antiquewhite;
	border: black;
	border-width: 1px;
	border-style: solid; */
}
.el-card{
	margin-top: 20px;
	margin-left: 20px;
	margin-right: 20px;
}
</style>

