<template>
	<div class="box">
		<h1>IOT</h1>
		<h4>,<a href="/">></a></h4>
		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px"
			style="text-align: left">
			<el-form-item label="/" prop="phone">
				<el-input v-model="ruleForm.phone"></el-input>
				<el-button type="primary" @click="getCode" class="btn"></el-button>
			</el-form-item>

			<el-form-item label="" prop="code">
				<el-input v-model="ruleForm.code"></el-input>
			</el-form-item>

			<el-form-item label="" prop="pass">
				<el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
			</el-form-item>

			<el-form-item label="" prop="checkPass">
				<el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
			</el-form-item>

			<!-- <el-form-item label="" prop="identity">
				<el-radio-group v-model="ruleForm.identity">
					<el-radio label="" ></el-radio>
					<el-radio label="" ></el-radio>
					<el-radio label="" ></el-radio>
				</el-radio-group>
			</el-form-item> -->

			<el-form-item>
				<el-button type="primary" @click="submit"></el-button>
				<el-button @click="resetForm('ruleForm')"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import axios from 'axios'
	import Qs from 'qs';
	import sha256 from 'js-sha256';
	export default {
		data() {
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''))
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass')
					}
					callback()
				}
			}
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''))
				} else if (value !== this.ruleForm.pass) {
					callback(new Error('!'))
				} else {
					callback()
				}
			}
			return {
				user_id: '',
				token: '',
				company_id:'',
				modal:'',
				way: false,
				ruleForm: {
					pass: '',
					checkPass: '',
					code: '',
					// identity: '',
					phone: '',
					sms_id: '',
				},
				rules: {
					pass: [{
							validator: validatePass,
							trigger: 'blur'
						},
						{
							required: true,
							message: '',
							trigger: 'blur'
						},
					],
					checkPass: [{
							validator: validatePass2,
							trigger: 'blur'
						},
						{
							required: true,
							message: '',
							trigger: 'blur'
						},
					],
					// identity:[
					// 	{ required: true, message: '', trigger: 'change'},
					// ],
					phone: [{
						required: true,
						message: '/',
						trigger: 'blur'
					}, ],
					code: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
				},
			}
		},
		mounted() {
			// this.getNameCookie();
			this.getIdCookier();
			if(this.user_id!==''&&this.token!==''&&this.company_id!==''&&this.modal!==''){
				this.$router.push({
					name: 'MainPage',
				})
			}
		},
		methods: {

			//
			submit() {
				let sha256 = require("js-sha256").sha256 //require
				var sha256password = sha256(this.ruleForm.pass) //
				// console.log(this.sha256password)//
				var data;
				if (/^(0|86|17951)?(13[0-9]|15[012356789]|17[0-8]|18[0-9]|14[5679]|19[189]|16[56])[0-9]{8}$/.test(this.ruleForm.phone)) {
					data = {
						"requestCode": "002",
						"phoneNumber": this.ruleForm.phone,
						"code": this.ruleForm.code,
						"sms_id": this.ruleForm.sms_id,
						"password": sha256password,
					}
				} else if (/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+$/.test(this.ruleForm.phone)) {
					data = {
						"requestCode": "004",
						"address": this.ruleForm.phone,
						"code": this.ruleForm.code,
						"sms_id": this.ruleForm.sms_id,
						"password": sha256password,
					}
				} else {
					console.log('');
					return;
				}
				let _this = this;
				var url = 'https://site.maple.today/IOTplatform/SMS';
				
				var AXIOS = axios.create({
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded'
					}
				});
				AXIOS.post(url, Qs.stringify(data))
					.then(res => {
						if (res.data.result === "1") {
							console.log('');
							_this.setIdCookie(res.data.user_id,res.data.token,res.data.company,res.data.modal,1);
							_this.$router.push({
								name:'MainPage',
							})
						} else if (res.data.result === "0") {
							alert('')
						} else if (res.data.result === "2") {
							alert('')
						}
					})
					.catch(error => console.log(error));
			},

			//
			resetForm(ruleForm) {
				this.$refs[ruleForm].resetFields();
			},

			//
			getCode() {
				let _this = this;
				var url;
				var data;
				if (/^(0|86|17951)?(13[0-9]|15[012356789]|17[0-8]|18[0-9]|14[5679]|19[189]|16[56])[0-9]{8}$/.test(this.ruleForm.phone)) {
					url = 'https://site.maple.today/IOTplatform/SMS';
					data = {
						"requestCode": "001",
						"phoneNumber": this.ruleForm.phone,
					}
				} else if (/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+$/.test(this.ruleForm.phone)) {
					url = 'https://site.maple.today/IOTplatform/SMS';
					data = {
						"requestCode": "003",
						"address": this.ruleForm.phone,
					}
				} else {
					console.log('');
				}

				
				console.log(Qs.stringify(data))
				var AXIOS = axios.create({
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded'
					}
				});
				AXIOS.post(url, Qs.stringify(data))
					.then(res => {
						if (res.data.result === "1") {
							_this.ruleForm.sms_id = res.data.sms_id;
							console.log(_this.ruleForm.sms_id);
						} else if (res.data.result === "0") {
							alert('')
						} else if (res.data.result === "2") {
							alert('1')
						}
						else{
							console.log(res)
						}
					})
					.catch(error => console.log(error));
			},
			//setNameCookie
			setNameCookie(c_name, c_pwd, exdays) {
				var exdate = new Date() //
				exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) //
				//cookie
				window.document.cookie = 'userName' + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'userPwd' + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
			},
			setIdCookie(c_id,c_token,c_companyid,c_modal,exdays){
				//c_idid
				//c_tokentoken
				//c_companyidid
				//c_modal
				var exdate = new Date() //
				exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) //
				//cookie
				window.document.cookie = 'user_id' + '=' + c_id + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'token' + '=' + c_token + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'company_id' + '=' + c_companyid + ';path=/;expires=' + exdate.toGMTString()
				window.document.cookie = 'modal' + '=' + c_modal + ';path=/;expires=' + exdate.toGMTString()
			},
			//cookie
			getNameCookie: function() {
				if (document.cookie.length > 0) {
					this.checked = true
					// console.log(document.cookie)
					var arr = document.cookie.split('; ') //
					for (var i = 0; i < arr.length; i++) {
						var arr2 = arr[i].split('=') //
						//
						if (arr2[0] === 'userName') {
							this.ruleForm.name = arr2[1] //
						}
						if (arr2[0] === 'userPwd') {
							this.ruleForm.password = arr2[1]
						}
					}
				}
			},
			getIdCookier: function(){
				if (document.cookie.length > 0) {
					// console.log(document.cookie)
					var arr = document.cookie.split('; ') //
					for (var i = 0; i < arr.length; i++) {
						var arr2 = arr[i].split('=') //
						//
						if (arr2[0] === 'user_id') {
							this.user_id = arr2[1] //
						}
						if (arr2[0] === 'token') {
							this.token = arr2[1]
						}
						if (arr2[0] === 'company_id') {
							this.company_id = arr2[1]
						}
						if (arr2[0] === 'modal') {
							this.modal = arr2[1]
						}
					}
				}
			},
			//cookie
			clearNameCookie: function() {
				this.setNameCookie('', '', -1) //21
			},
			clearIdCookie: function() {
				this.setIdCookie('', '','','', -1) //21
			},
		},
	}
</script>
<style>
	.box {
		width: 650px;
		text-align: center;
		margin: 0 auto;
	}

	a {
		text-decoration: none;
		color: #97caff;
	}

	.btn {}
</style>
